@import "~react-image-gallery/styles/css/image-gallery.css";

/* mobile styling */

.image-gallery-thumbnails,.image-gallery-fullscreen-button,
.image-gallery-play-button{
    display: none;
}
.image-gallery-image,
.image-gallery-slides{
    width: 100%;
}

/* mobile styling */

/* product info */

.product__info{
    padding: 1rem;
}
main{
    position: relative;
    z-index: 1;
}
.brand{
    color: var(--Orange);
    font-size: .8rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;

}
.title{
    color: var(--Veryvarkblue);
    font-size: 2.1rem;
    line-height: 2.3rem;
    padding-top: .7rem ;
}
.details{
    color: var(--Darkgrayishblue);
    line-height: 1.5rem;
    padding: 1rem 0;
    
}
.pricing{
    display: flex;
    align-items: center;
}
.price{
    color: var(--Veryvarkblue);
    font-size: 2rem ;
    font-weight: 700;
}
.per{
    background-color: var(--Paleorange);
    font-weight: 700;
    color: var(--Orange);
    border-radius: 5px;
    padding: 1px 6px;
    margin-left: 1.2rem;
}
.full-price{
    font-weight: 700;
    color: var(--Grayishblue);
    margin-left: auto;
    text-decoration: line-through;
}
/* product info */

/* amount */
.forma{
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    margin-top: 1rem ;
}
#amount{
    text-align: center;
    padding: .7rem 0;
    border-radius: 8px;
    font-size: 1.1rem;
    background-color: var(--Lightgrayishblue);
    position: relative;
    border: none;
    width: 100%;
    font-weight: 700;
}
.counter{
    display: flex;
    position: relative;
}
.counter button{
    display: flex;
    align-items: center;
    padding-left: .5rem;
    padding-right: .5rem;
    position: absolute;
    height: 100%;
    border: none;
    background-color: transparent;
    cursor: pointer;
}
.plus{
    right: 10px;
}
.minus{
    left: 10px;
}
.submit-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: .5rem;
    padding:1rem .5rem;
    color: white;
    background-color: var(--Orange);
    border-radius: 8px;
    border: none;
    font-size: 1.1rem;
    font-weight: 700;
    font-family: inherit;
    cursor: pointer;
    box-shadow: 10px 10px 5px 0px rgba(255,125,26,0.20);
    -webkit-box-shadow: 10px 10px 5px 0px rgba(255,125,26,0.20);
    -moz-box-shadow: 10px 10px 5px 0px rgba(255,125,26,0.20);
    margin-bottom: 1.5rem;
}

/* amount */

@media (min-width:1020px){
    .image-gallery-thumbnails,.image-gallery-fullscreen-button{
    display: block;
    }
    .image-gallery-image,
    .image-gallery-slides,.image-gallery-slide{
        width: 100%;
        border-radius: 8px;
    }
    main{
        display: grid;
        grid-template-columns: 50% 50%;
        padding: 2.5rem;
        grid-gap: 1.5rem;
    }
    main .left, .fullscreen{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
    main .right{
        padding-right: 5.5rem;
        margin-top: 5rem;
    }
    .title{
        font-size: 2.5rem;
        padding: 1rem 0;
    }
    .details{
        max-width: 55ch;
    }
    .fullscreen{
        margin-top: 2.5rem;
    }
    .image-gallery{
        max-width: 500px;
        border-radius: 8px;
    }
    .image-gallery-slides{
        max-height: 490px;
        border-radius: 10px;
        max-width: 600px;
        overflow: hidden;
    }
    .image-gallery-thumbnails-container{
        display: flex;
        justify-content: space-between;
        margin-top: 1rem;
    }
    .image-gallery-thumbnail-image{
        border-radius: 10px;
    }
    .full-price{
        display: block;
    }

    .pricing{
        display: inline;
    }
    .forma{
        flex-direction: row;
        column-gap: 1rem;
        width: 80%;
    }
    #amount{
        text-align: center;
        padding: .7rem 0;
        border-radius: 8px;
        font-size: 1.1rem;
        background-color: var(--Lightgrayishblue);
        position: relative;
        border: none;
        width: 100%;
        font-weight: 700;
    }
    .counter{
        width: 35%;
        max-height: 55px;
    }
    .submit-btn{
        width: 75%;
    }

}