@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&family=Roboto:wght@100&display=swap');
:root{
    --Orange: hsl(26, 100%, 55%);
--Paleorange: hsl(25, 100%, 94%);

/* ### Neutral */

--Veryvarkblue: hsl(220, 13%, 13%);
--Darkgrayishblue: hsl(219, 9%, 45%);
--Grayishblue: hsl(220, 14%, 75%);
--Lightgrayishblue: hsl(223, 64%, 98%);
--White: hsl(0, 0%, 100%);
/* (with 75% opacity for lightbox background) */
--Black : hsla(0, 0%, 0%,75%);
--shadow-color: 0deg 0% 63%;

--shadow-elevation-medium:
0px 0.5px 0.6px hsl(var(--shadow-color) / 0.36),
-0.1px 1.5px 1.7px -0.8px hsl(var(--shadow-color) / 0.36),
-0.3px 3.8px 4.3px -1.7px hsl(var(--shadow-color) / 0.36),
-0.6px 9.4px 10.6px -2.5px hsl(var(--shadow-color) / 0.36);
}
html{
    box-sizing: border-box;

}
*,*::after,*::before{
    box-sizing: inherit;
    padding: 0;
    margin: 0;

}