.modal-content {
    background-color: #fefefe;
    /* 15% from the top and centered */

    border: 1px solid #888;
     /* Could be more or less, depending on screen size */
    height: 100%;
    transition: 500ms ease-in-out;
    position: fixed;
    z-index: 99;
}
.modal-active{
    padding: 20px;
    width: 70%;
    transition: 400ms ease-in-out;
}
.modal-closed{
    left: -70%;
    padding: 0;
    transition: 400ms ease-in-out;
}
.close{
    cursor: pointer;
    position: relative;
}
.mobile-menu{
    padding-top: 1.2rem;
    position: relative;
    z-index: 99;
}
.mobile-menu ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    row-gap: .8rem;
}
.mobile-menu ul li{
    font-weight: 700;
    cursor: pointer;
    position: relative;
    z-index: 99;
}