.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    max-height: 80px;
    position: relative;
    z-index: 2;
}
.navbar::after{
    content: "";
    width: 100%;
    height: 3px;
    background-color: var(--Lightgrayishblue);
    position: absolute;
    bottom: 0;
}
.profile{
    
    transition: 400ms ease-in-out;
    cursor: pointer;
    width: 50px;
}

.profile img{
    max-height: 46px;
    transform: translateY(3px);
    transition: 300ms ease-in-out;
    border-radius: 50%;
    border: 2px solid transparent;
}
.profile img:hover{
    border: 2px solid var(--Orange);
    border-radius: 50%;
}

.navbar .left, .navbar .right{
    display: flex;
    align-items: center;
    column-gap: 1.2rem;
}
.cart{
    cursor: pointer;
}
.menu.mobile{
    cursor: pointer;
}

/* dropdown */

.dropdown.active > .link,
.link:hover {
  color: black;
}



.dropdown-menu {
  opacity: 0;
  pointer-events: none;
  transform: translateY(-10px);

}
.drop{
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 90%;
    top: calc(100% + .25rem);
    background-color: white;
    padding: .75rem;
    border-radius: .25rem;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .1);
    transition: opacity 150ms ease-in-out, transform 150ms ease-in-out;
    z-index: 3;
}
.drop div{
    width: 100%;
}
.dropdown-menu-active {
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
  transition: opacity 150ms ease-in-out, transform 150ms ease-in-out;
}



.dropdown-links {
  display: flex;
  flex-direction: column;
  gap: .45rem;
}
.dropdown-heading{
    position: relative;
    width: 100%;
    padding: .75rem 0;
    font-weight: 500;
}
.dropdown-heading::after{
  content: "";
  position: absolute;
  bottom: 0;
  left: -.75rem;
  width: calc(100% + 1.5rem);
  height: 3px;
  background-color: var(--Lightgrayishblue);
}
.cart-product{

    padding:1rem;
}
.cart__content{
    display: flex;
    align-items: center;
}
.empty{
    width: 100%;
    text-align: center;
    color: var(--Darkgrayishblue);
    padding: 2.5rem 0;
    font-weight: 500;
    
}
.thumb img{
    max-height: 60px;
    max-width: 60px;
    border-radius: 8px;
}
.thumb{
    padding-right: 1rem;
}
.total{
    color: var(--Veryvarkblue);
    font-weight: 700;
    padding-left: .6rem;
}
.btn__checkout{
    background-color: var(--Orange);
    color: white;
    text-align: center;
    width: 100%;
    padding: 1rem 0;
    font-family: inherit;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 700;
    margin-top: .5rem;

}
.trash{
    z-index: 3;
}
.navigation{
    display: none;
}
.navigation ul li a::after{
    display: none;
}
.navbar::after{
    display: none;
}


@media (min-width:900px) {
    .dropdown {
        position: relative;
    }
    .navbar{
        margin: .5rem 2.5rem;
    }
    .menu.mobile{
    display: none;
    }
    .navigation{
        margin-left: 1.5rem;
    }
    .navigation, .navigation ul{
        display: flex;
        align-items: center;
    }
    .navigation ul{
        column-gap: 1rem;
        list-style: none;
    }
    .right{
        position: relative;
        z-index: 3;
    }
    .navigation ul li a{
        text-decoration: none;
        color: var(--Darkgrayishblue);
        position: relative;
    }
    .navigation ul li a::after{
        content: "";
        display: flex;
        position: absolute;
        left: 0;
        bottom: -27px;
        width: 0;
        height: 0;
        background-color: var(--Orange);
        transition: 300ms ease-in-out;
    }
    .navigation ul li a:hover:after{
        width: 100%;
        height: 3px;

    }
    .drop {
        width: 300px;
        left: -150px;
        box-shadow: var(--shadow-elevation-medium);
        z-index: 3;

    }
    .dropdown-links,.cart-product,.btn__checkout{
        position: relative;
        z-index: 3;
    }
    .title__cart{
        font-weight: 300;
        font-size: 1rem;
    }

    .cart{
        margin-right: 1.5rem;
        transform: translateY(3px);
    }
    .navbar::after{
        display: flex;
    }
}